@import "ScssHelpers/helpers-all";
.step_form__wrap {
	h4 {
		@include h4;
		text-align: center;
		letter-spacing: -0.04rem;

		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}
}

.step_form__list {
	@include flex_block(row, wrap, flex-start, stretch);

	@include bp($point_2, min) {
		margin: 0 -2.6rem -2.6rem 0;
	}

	@include bp($point_3) {
		margin: 0 -1.2rem -1.2rem 0;
	}
}

.step_form__list--bottom_spacing_mod {
	&:not(:last-child) {
		margin-bottom: 3rem;
	}
}

.step_form__item {
	height: auto;

	@include bp($point_2, min) {
		width: 25%;
		padding: 0 2.6rem 2.6rem 0;
	}

	@include bp($point_3, $point_5) {
		width: 50%;
		padding: 0 1.2rem 1.2rem 0;
	}

	@include bp($point_5 - 1) {
		width: 100%;
		padding: 0 1.2rem 1.2rem 0;
	}

	.step_form__list--2_col_mod & {
		@include bp($point_5, min) {
			width: 50%;
		}

		@include bp($point_5 - 1) {
			width: 100%;
		}
	}

	.step_form__list--3_col_mod & {
		@include bp($point_2, min) {
			width: 33.3333%;
		}

		@include bp($point_3, $point_5) {
			width: 50%;
		}

		@include bp($point_5 - 1) {
			width: 100%;
		}
	}
	.step_form__list--4_col_mod & {
		@include bp($point_2, min) {
			width: 25%;
			padding: 0 2.4rem 2.4rem 0;
		}

		@include bp($point_3, $point_5) {
			width: 50%;
		}

		@include bp($point_5 - 1) {
			width: 100%;
		}
	}
}

.step_form__btn {
	display: flex;
	align-items: center;
	justify-content: center;

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 3.8rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 4.4rem;
		}
	}
}

.step_form__body {
	height: 0;
	opacity: 0;
	transform: translateY(-2rem);

	transition: opacity 0.3s ease, transform 0.3s ease;

	.onboarding_2--active_state & {
		height: 100%;
		opacity: 1;
		transform: translateY(0);
	}

	@include bp($point_3) {
		display: none;
	}
}
