@import "ScssHelpers/helpers-all.scss";

.quiz_item {
	position: relative;
	display: block;
	height: 100%;

	border-radius: .8rem;

	transition: box-shadow 0.3s ease;

	cursor: pointer;
	@media (any-hover: hover) {
		&:hover {
			box-shadow: 0 4px 0.8rem rgba(0, 0, 0, 0.2);
		}
	}

	&.quiz_item--gray_mod {
		border-radius: 0;
		background-color: $gray_3;
	}
}

.quiz_item__input {
	position: absolute;

	padding: .4rem;
	margin: 0;

	background-color: $gold_2;
	border-radius: 50%;

	opacity: 0;
	color: $white;
	transition: opacity .3s ease;

	cursor: pointer;

	outline: none;
	-webkit-appearance: none;
	-moz-appearance: none;

	@include bp($point_2, min) {
		right: 1.7rem;
		//top: 1.9rem;
		top: 2.2rem;
		height: 3.2rem;
		width: 3.2rem;
	}

	@include bp($point_3) {
		top: 1.4rem;
		right: 1.4rem;
		width: 2.8rem;
		height: 2.8rem;
	}

	&:after {
		position: absolute;
		top: 42%;

		display: block;
		height: 1.4rem;
		width: .8rem;

		content: "";

		border: .3rem solid #fff;
		border-top: 0;
		border-left: 0;
		transform: rotate(45deg) translate(-39%, -46%);

		@include bp($point_2, min) {
			left: 29%;
			height: 1.4rem;
			width: .8rem;
		}

		@include bp($point_3) {
			left: 27%;
			height: 1.2rem;
			width: 0.6rem;
		}
	}
}

.quiz_item__input:checked {
	opacity: 1;
}

.quiz_item__body {
	height: 100%;

	border-radius: .8rem;
	box-shadow: inset 0 0 0 1px $beige_2;
	transition: box-shadow .3s ease-out;

	@include bp($point_2, min) {
		padding: 2.8rem 1rem 3.8rem 3.1rem;
	}

	@include bp($point_3) {
		padding: 1.8rem 2rem 2.2rem 2.3rem;
	}

	// &.quiz_item__body--active_state {
	// 	padding: 1.8rem 2rem 2.8rem 2.3rem;
	// }

	.step_form__list--2_col_mod & {
		@include bp($point_2, min) {
			padding: 3.1rem 1rem 5.1rem 4.1rem;
		}

		@include bp($point_3) {
			padding: 2rem 2rem 2.1rem 2.3rem;
		}
	}

	.step_form__list--3_col_mod & {

		@include bp($point_2, min) {
			padding: 3.2rem 1rem 4.1rem 4.1rem;
		}

		@include bp($point_3) {
			padding: 2rem 2rem 2.1rem 2.3rem;
		}
	}

	.step_form__list--4_col_mod & {
		padding-top: 2.8rem;
		padding-right: 3.2rem;
		padding-bottom: 4rem;
	}

	& > *:first-child {
		padding-right: 4.6rem;
	}

	.quiz_item--gray_mod & {
		box-shadow: inset 0 0 0 0 $beige_2;
	}

	.quiz_item--simple_mod & {
		@include bp($point_2, min) {
			padding: 2.4rem 2.8rem 2.9rem;
		}
	}
}

.quiz_item__input:checked ~ .quiz_item__body {

	box-shadow: inset 0 0 0 3px $gold_2;

	.quiz_item--acc_mod & {
		@include bp($point_3) {
			padding: 1.8rem 2rem 2.8rem 2.3rem;
		}
	}
}

.quiz_item__title {
	@include h3;

	line-height: 0.96;
	word-wrap: normal;

	@include bp($point_3) {
		letter-spacing: -.03em;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.8rem;
		}
		@include bp($point_3) {
			margin-bottom: .4rem;
		}
	}

	span {
		font-weight: 400;
		line-height: 1.16;
		letter-spacing: -0.01em;

		@include bp($point_2, min) {
			font-size: 2.4rem;
		}
		@include bp($point_3) {
			font-size: 1.8rem;
		}
	}

	.step_form__list--4_col_mod & {
		@include h4;

		&:not(:last-child) {
			margin-bottom: 2.5rem;
		}
	}
}

.quiz_item__subtitle {
	@include text;

	font-weight: 500;
	color: $gray_2;
	letter-spacing: 0.086em;
	line-height: 1.5;
	text-transform: uppercase;

	&:not(:last-child) {
		margin-bottom: 1.5rem;
	}

	.step_form__list--2_col_mod &,
	.step_form__list--3_col_mod & {
		margin-bottom: 0.9rem;
	}

	@include bp($point_3) {
		display: none;
	}
}

.quiz_item__cost {
	@include text;

	line-height: 1.5;
	font-weight: 500;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: .3rem;
		}
		@include bp($point_3) {
			margin-bottom: .8rem;
		}
	}

	@include bp($point_3) {
		text-transform: uppercase;
		color: $gray_2;
		letter-spacing: 0.07em;
	}
}

.quiz_item__description {
	@include text;

	line-height: 1.5;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: .5rem;
		}
		@include bp($point_3) {
			margin-bottom: .7rem;
		}
	}

	@include bp($point_3) {
		letter-spacing: -0.015em;
	}

	.step_form__list--2_col_mod &,
	.step_form__list--3_col_mod & {
		@include bp($point_2, min) {
			padding-right: 2rem;
		}
	}
}

.quiz_item__list {

	// @include bp($point_3) {
	// 	position: relative;
	// 	max-height: 0;
	// 	overflow: hidden;
	// 	transition: all .3s ease;
	// }
}

.quiz_item__list_item {
	@include text;

	position: relative;

	line-height: 1.5;

	@include bp($point_2, min) {
		padding-left: 2.8rem;
	}
	@include bp($point_3) {
		padding-left: 3.6rem;
	}

	&::after {
		position: absolute;
		top: 50%;

		content: "";

		border-radius: 50%;
		background-color: $black;

		transform: translateY(-50%);

		@include bp($point_2, min) {
			left: .4rem;
			width: .5rem;
			height: .5rem;
		}
		@include bp($point_3) {
			left: 1rem;
			width: 3px;
			height: 3px;
		}
	}
}

.quiz_item__img_w {
	display: block;
	width: 100%;
	height: 15.4rem;

	margin-bottom: 2.2rem;
}

.quiz_item__img {
	@include cover_img;
}

.quiz_item__content {
	.quiz_item__input:not(:checked) ~ .quiz_item__body & {
		.quiz_item--acc_mod & {
			@include bp($point_3) {
				padding-right: 4.2rem;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
				overflow: hidden;
			}
		}
	}

	// .quiz_item__body:not(.quiz_item__body--active_state) & {
	// 	padding-right: 4.2rem;
	// 	text-overflow: ellipsis;
	// 	display: -webkit-box;
	// 	-webkit-line-clamp: 2;
	// 	-webkit-box-orient: vertical;
	// 	overflow: hidden;
	// }
}

.quiz_item__acc_trigger {
	position: absolute;
	right: 2rem;
	bottom: 2rem;

	width: 2.4rem;
	height: 2.4rem;
	padding: .8rem .4rem;
	color: $gold_2;
	pointer-events: none;

	.quiz_item__input:checked ~ .quiz_item__body & {
		display: none;
	}

	@include bp($point_2, min) {
		display: none;
	}

	// .quiz_item__body--active_state & {
	// 	display: none;
	// }
}
